<script>
import $ from 'jquery';

export default {
  methods: {
  clientSlideShow(){
        let clients = $('.intro div.slide')
        let self = this
        $.each( clients, function( index ){
          (function(that, i) {
            setTimeout(function() { 
                $(that).css({'display': 'block' })
                $(that).prev().fadeOut().css({'display': 'none' })
                if ( index == clients.length - 1){
                    setTimeout( function(){
                      $(that).fadeOut().css({'display': 'none' })
                      self.clientSlideShow()
                    },2500)
                }
            }, 4000 * i);
          })(this, index);
        })
      }
     
   
  },
  created(){
    var self = this
    $( function(){
       self.clientSlideShow()
    })
  }
}
</script>

