import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Home'
import Aviso from '@/pages/Aviso'
import Clients from '@/pages/Clients'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/aviso',
      name: 'aviso',
      component: Aviso
    },
    {
      path: '/clients/',
      name: 'clients-page',
      component: Clients
    },
    {
      path: '/clients/:client',
      name: 'clients',
      component: Clients
    }
  ],
  mode: 'history'
})
